.vc-dialogue {
  z-index: 21;
}
.vc-dialogue .dialogue-hd {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 44px;
  color: #fff;
  z-index: 30;
  -webkit-transform: translateZ(1px);
          transform: translateZ(1px);
}
.vc-dialogue .dialogue-bd {
  width: 100%;
  height: 100%;
  padding: 44px 0 50px 0;
}
.vc-dialogue .dialogue-ft {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 50px;
  padding: 8px 10px;
  border-top: 1px solid #b7b7b7;
  background: #f2f1f0;
  z-index: 30;
}
.vc-dialogue .ft-left,
.vc-dialogue .ft-center,
.vc-dialogue .ft-right {
  display: inline-block;
  vertical-align: middle;
  line-height: 33px;
}
.vc-dialogue .ft-center {
  padding-left: 10px;
  padding-right: 10px;
}
.vc-dialogue .dialogue-input {
  width: 100%;
  height: 33px;
  line-height: 31px;
  padding-left: 10px;
  padding-right: 10px;
  border: 1px solid #b7b7b7;
  border-radius: 5px;
  background: #fff;
}
.vc-dialogue .dialogue-voice {
  height: 33px;
  line-height: 31px;
  border: 1px solid #b7b7b7;
  border-radius: 5px;
  text-align: center;
  color: #333;
}
.vc-dialogue .dialogue-btn {
  display: inline-block;
  font-size: 28px;
  color: #999;
}
.vc-dialogue .dialogue-item {
  width: 100%;
  padding: 10px;
}
.vc-dialogue .dialogue-img {
  width: 48px;
  height: 48px;
}
.vc-dialogue .dialogue-img img {
  width: 100%;
  height: 100%;
}
.vc-dialogue .dialogue-msg-txt {
  position: relative;
  font-size: 15px;
  line-height: 22px;
  padding: 12px;
  max-width: 220px;
  border-radius: 5px;
}
.vc-dialogue .dialogue-msg-txt:before {
  content: ' ';
  position: absolute;
  top: 10px;
  width: 0;
  height: 0;
  border-width: 10px;
  border-color: transparent;
  border-style: solid;
}
.vc-dialogue .dialogue-left,
.vc-dialogue .dialogue-right {
  position: relative;
}
.vc-dialogue .dialogue-left .dialogue-msg {
  width: 100%;
  padding-left: 68px;
  text-align: left;
}
.vc-dialogue .dialogue-left .dialogue-msg-txt {
  display: inline-block;
  background: #fff;
}
.vc-dialogue .dialogue-left .dialogue-msg-txt:before {
  left: -20px;
  border-right-color: #fff;
}
.vc-dialogue .dialogue-left .dialogue-img {
  position: absolute;
  top: 10px;
  left: 10px;
}
.vc-dialogue .dialogue-right .dialogue-msg {
  width: 100%;
  padding-right: 68px;
  text-align: right;
}
.vc-dialogue .dialogue-right .dialogue-msg-txt {
  display: inline-block;
  background: #a2e759;
  text-align: left;
}
.vc-dialogue .dialogue-right .dialogue-msg-txt:before {
  right: -20px;
  border-left-color: #a2e759;
}
.vc-dialogue .dialogue-right .dialogue-img {
  position: absolute;
  top: 10px;
  right: 10px;
}


