.vc-tabbar {
  position: relative;
  z-index: 10;
  border-top: 1px solid #b7b7b7;
  background: #f2f1f0;
}
.vc-tabbar .nav-links {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.vc-tabbar .nav-links_item {
  display: block;
  -webkit-box-flex: 1;
  -ms-flex: 1;
      flex: 1;
  font-size: 0;
  color: #999;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  padding: 2px 0 0;
}
.vc-tabbar .nav-links_item:hover {
  cursor: pointer;
}
.vc-tabbar .nav-links_item.v-link-active {
  color: #09bb07;
}
.vc-tabbar .nav-links_item_on {
  color: #09bb07;
}
.vc-tabbar .nav-links_icon {
  display: inline-block;
  width: 24px;
  height: 24px;
  font-size: 24px;
  line-height: 24px;
}
.vc-tabbar .nav-links_icon i {
  font-size: 26px;
}
.vc-tabbar .nav-links_label {
  font-size: 10px;
  margin-top: 3px;
  margin-bottom: 2px;
}


