.vc-header {
  width: 100%;
  height: 44px;
  padding: 10px;
  color: #fff;
  z-index: 3;
  background: -webkit-linear-gradient(top, #303036, #3c3b40);
  background: linear-gradient(180deg, #303036, #3c3b40);
}
.vc-header .action {
  top: 0;
  right: 0;
}
.vc-header .hd-left,
.vc-header .hd-center,
.vc-header .hd-right {
  height: 24px;
  line-height: 24px;
}
.vc-header .hd-left {
  width: 65px;
  padding-left: 20px;
  text-align: left;
}
.vc-header .hd-left span {
  display: inline-block;
  text-align: left;
  vertical-align: middle;
  font-size: 15px;
}
.vc-header .hd-center {
  text-align: center;
}
.vc-header .hd-right {
  width: 65px;
  padding-right: 5px;
  text-align: right;
}
.vc-header .hd-right span {
  font-size: 18px;
  text-align: right;
}
.vc-header .back-arrow {
  position: relative;
}
.vc-header .back-arrow:after {
  content: ' ';
  position: absolute;
  top: 50%;
  left: -14px;
  display: inline-block;
  width: 12px;
  height: 12px;
  border-width: 0 0 3px 3px;
  border-color: #fff;
  border-style: solid;
  margin-top: -8px;
  background: transparent;
  -webkit-transform: rotateZ(45deg);
          transform: rotateZ(45deg);
}
.vc-header .hd-title {
  font-size: 18px;
}


