.vc-search-bar {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 44px;
  padding: 8px;
  line-height: 1;
}
.vc-search-bar .search-icon {
  color: #8d8e92;
}
.vc-search-bar .search-label {
  position: absolute;
  left: 8px;
  right: 8px;
  height: 28px;
  line-height: 26px;
  background: #fff;
  border-radius: 3px;
  border: 1px solid #e5e5ea;
  text-align: center;
  color: #8f8d94;
  font-size: 14px;
  z-index: 2;
}
.vc-search-bar .search-label span {
  display: inline-block;
}
.vc-search-bar .search-label i,
.vc-search-bar .search-label span {
  height: 26px;
  line-height: 26px;
}
.vc-search-bar .search-box {
  position: relative;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  line-height: 28px;
  background: #fff;
  padding: 0 30px;
  border-radius: 3px;
  z-index: 1;
}
.vc-search-bar .search-box .search-icon {
  position: absolute;
  top: 0;
  left: 10px;
  line-height: 28px;
  vertical-align: middle;
}
.vc-search-bar .search-input {
  width: 100%;
  height: 28px;
  line-height: 28px;
  padding: 4px 0;
  border: none;
  font-size: 14px;
}
.vc-search-bar .search-input::-webkit-input-placeholder {
  color: #8d8e92;
}
.vc-search-bar .search-input:-moz-placeholder {
  color: #8d8e92;
}
.vc-search-bar .search-input::-moz-placeholder {
  color: #8d8e92;
}
.vc-search-bar .search-input:-ms-input-placeholder {
  color: #8d8e92;
}
.vc-search-bar .search-clear {
  position: absolute;
  top: 6px;
  right: 10px;
  width: 16px;
  height: 16px;
  line-height: 16px;
  font-size: 14px;
  text-align: center;
  border-radius: 50%;
  color: #fff;
  background: #efeff4;
}
.vc-search-bar .search-cancel {
  margin-left: 10px;
  line-height: 28px;
  color: #09bb07;
  white-space: nowrap;
}


