.vc-personinfo {
  z-index: 21;
}
.vc-personinfo .personinfo-hd {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 44px;
  color: #fff;
  background: -webkit-linear-gradient(top, #303036, #3c3b40);
  background: linear-gradient(180deg, #303036, #3c3b40);
  z-index: 30;
}
.vc-personinfo .personinfo-bd {
  width: 100%;
  height: 100%;
  padding: 44px 0 50px 0;
}
.vc-personinfo .item-bd-3 .item-title {
  height: 24px;
  line-height: 24px;
}
.vc-personinfo .item-bd-3 .item-txt {
  height: 18px;
  line-height: 18px;
  color: #888;
}


