.vc-action {
  position: relative;
  height: 24px;
}
.vc-action .action-bar_open {
  display: inline-block;
}
.vc-action .action-bar_open i {
  font-size: 20px;
}
.vc-action .action-bar_list {
  background-color: #49484b;
  font-size: 16px;
  width: 133px;
  padding: 0 15px;
  border-radius: 4px;
  top: 40px;
  right: -8px;
  -webkit-transform-origin: 90% 0;
          transform-origin: 90% 0;
  z-index: 1010;
}
.vc-action .action-bar_list:before {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  top: -8px;
  right: 12px;
  border-width: 0 6px 8px;
  border-color: transparent transparent #49484b;
  border-style: solid;
}
.vc-action .action-bar_item {
  line-height: 44px;
  text-align: left;
  position: relative;
}
.vc-action .action-bar_item:not(:last-child):after {
  content: "";
  width: 200%;
  height: 1px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #5b5b5d;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
  -webkit-transform-origin: 0 100%;
          transform-origin: 0 100%;
}
.vc-action .nav-links_icon,
.vc-action .nav-links_label {
  display: inline-block;
  vertical-align: middle;
}
.vc-action .nav-links_icon {
  width: 16px;
  margin-right: 10px;
  text-align: center;
}
.vc-action .nav-links_label {
  font-size: 14px;
}
.vc-action .action-bar_mask {
  position: absolute;
  width: 6.4rem;
  height: 11.38rem;
  right: -15px;
  top: 34px;
  z-index: 1000;
}
.tips-open {
  opacity: 1;
  -webkit-transition: initial;
  transition: initial;
}
.tips-close {
  opacity: 0;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transition: opacity 0.2s ease, -webkit-transform 0.4s ease;
  transition: opacity 0.2s ease, -webkit-transform 0.4s ease;
  transition: opacity 0.2s ease, transform 0.4s ease;
  transition: opacity 0.2s ease, transform 0.4s ease, -webkit-transform 0.4s ease;
}


